import SelectTime from "../../plugin/components/SelectTime";
import InfoTable from "../../plugin/components/InfoTable";
import PageInfo from "../../plugin/script/common/PageInfo";
import BaseIframe from "../../plugin/script/common/BaseIframe";
import PermissionButton from "../../plugin/components/PermissionButton";
import UserHandler from "../../script/handlers/UserHandler";
import InputArea from "../../plugin/components/InputArea";
import InfoTableItem from "../../plugin/components/InfoTableItem";

export default {
  name: "function_sort_config",
  extends: BaseIframe,
  components: {
    InfoTableItem,
    InputArea,
    SelectTime,
    InfoTable,
    PermissionButton
  },
  data() {
    return {
      pagesize: 10,
      currentPage: 1,
      total: 0,
      dialogTitle: "",
      value: true,
      labelPosition: "left", //左对齐
      dialogFormVisible: false,
      platforms: [
        { value: 1, label: "平台1" },
        { value: 2, label: "平台2" }
      ],
      format: {
        platform: ""
      },
      funOpts: [],
      channelOpts: [],
      tableStyle: [
        { label: "ID", prop: "id", width: "" },
        { label: "渠道名称", prop: "channel_name", width: "" },
        { label: "渠道KEY", prop: "channel_code", width: "" },
        { label: "活动1", prop: "func_1", width: "" },
        { label: "活动2", prop: "func_2", width: "" },
        { label: "活动3", prop: "func_3", width: "" },
        { label: "活动4", prop: "func_4", width: "" },
        { label: "活动5", prop: "func_5", width: "" },
        { label: "活动6", prop: "func_6", width: "" },
        { label: "操作者", prop: "auth", width: "" },
        { label: "创建时间", prop: "create_time", width: "160" },
        { label: "操作", prop: "action", width: "150" }
      ],
      records: [],
      pageInfo: new PageInfo(0, [5, 10, 15], 5),
      dialogAddVisible: false,
      form: {
        checkList: [],
        active_1: "",
        active_2: "",
        active_3: "",
        active_4: "",
        active_5: "",
        active_6: ""
      }
    };
  },
  methods: {
    resetForm() {
      this.form = {
        id: null,
        checkList: [],
        active_1: "",
        active_2: "",
        active_3: "",
        active_4: "",
        active_5: "",
        active_6: ""
      };
    },
    getFunSortList() {
      this.$http
        .get("v1/backend/lobby/top", {
          params: {
            page: this.currentPage,
            limit: this.pagesize,
            type: 2
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.code === 1) {
            // if(res.data.total === 0) {
            //   this.currentPage = 1
            //   this.getFunSortList()
            // }
            this.records = res.data.data;
            this.total = res.data.total;
            this.func_list_index();
            console.log(this.records);
          }
        });
    },
    func_list_index: function() {
      this.records.map((item1, index1) => {
        JSON.parse(item1.func_list).map((item2, index2) => {
          item1[`func_${index2 + 1}`] = item2;
        });
      });
    },
    /**搜索*/
    search() {},
    openAddDialog() {
      this.dialogTitle = "添加活动入口配置";
      this.dialogFormVisible = true;
      this.resetForm();
      this.getActiveOpts();
      this.getAddChannelList();
      this.form.active_1 = this.funOpts[0];
      this.form.active_2 = [1].id
      this.form.active_3 = [2].id
      this.form.active_4 = [3].id
      this.form.active_5 = [4].id
      this.form.active_6 = [5].id
    },
    addFunSortConf() {
      if (!this.form.id) {
        let actStr =
          this.form.active_1 +
          "," +
          this.form.active_2 +
          "," +
          this.form.active_3 +
          "," +
          this.form.active_4 +
          "," +
          this.form.active_5 +
          "," +
          this.form.active_6;
        let actArr = actStr.split(",");
        let data = {
          name: this.form.checkList[0],
          code: this.form.checkList[0],
          list_id: JSON.stringify(actArr),
          type: 2
        };
        this.$http.post("v1/backend/lobby/top", data).then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.dialogFormVisible = false;
            this.getFunSortList();
          }
        });
      } else {
        let actStr =
          this.form.active_1 +
          "," +
          this.form.active_2 +
          "," +
          this.form.active_3 +
          "," +
          this.form.active_4 +
          "," +
          this.form.active_5 +
          "," +
          this.form.active_6;
        let actArr = actStr.split(",");
        let data = {
          name: this.form.checkList[0],
          code: this.form.checkList[0],
          list_id: JSON.stringify(actArr),
          type: 2,
          banner_id: this.form.id
        };
        this.$http.put("v1/backend/lobby/top", data).then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.dialogFormVisible = false;
            this.getFunSortList();
          }
        });
      }
    },
    handleEdit(row) {
      console.log(row);
      this.dialogTitle = "更新活动入口配置";
      this.dialogFormVisible = true;
      this.getActiveOpts();
      this.getChannelList();
      this.form.id = row.id;
      this.form.checkList = [row.channel_name];
      this.form.active_1 = row.func_1;
      this.form.active_2 = row.func_2;
      this.form.active_3 = row.func_3;
      this.form.active_4 = row.func_4;
      this.form.active_5 = row.func_5;
      this.form.active_6 = row.func_6;
    },
    handleDelete(row) {
      console.log(row);
      this.$confirm("确认删除吗？", "信息", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$http
            .delete("v1/backend/lobby/top", {
              params: {
                id: row.id
              }
            })
            .then(res => {
              console.log(res);
              if (res.data.code === 1) {
                this.getFunSortList();
                this.$message({
                  type: "success",
                  message: res.data.msg
                });
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    getAddChannelList() {
      let data = {
        type_id: 1,
        add_id: 1
      };
      this.$http.post("v1/backend/no_channel", data).then(res => {
        console.log(res);
        if (res.data.code === 1) {
          this.channelOpts = res.data.data;
        }
      });
    },
    //获取所有的渠道列表
    getChannelList() {
      let data = {
        type_id: 2,
        add_id: 1
      };
      this.$http.post("v1/backend/no_channel", data).then(res => {
        console.log(res);
        if (res.data.code === 1) {
          this.channelOpts = res.data.data;
        }
      });
    },
    getActiveOpts() {
      this.$http
        .get("v1/backend/lobby/name_type", {
          params: {
            type_id: 3
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.funOpts = res.data.data;
            // this.form.active_1 = String(res.data.data[0].id)
            // this.form.active_2 = String(res.data.data[0].id)
            // this.form.active_3 = String(res.data.data[0].id)
            // this.form.active_4 = String(res.data.data[0].id)
            // this.form.active_5 = String(res.data.data[0].id)
            // this.form.active_6 = String(res.data.data[0].id)
          }
        });
    },
    handleSizeChange(val) {
      this.pagesize = val;
      this.currentPage = 1;
      this.getFunSortList();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getFunSortList();
    }
  },
  mounted() {
    this.getFunSortList();
  }
};



